
'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { collection, onSnapshot, query } from 'firebase/firestore';
import { db } from '@/lib/firebase';

// Un componente visual para cada mascota en la lista
function MascotaCard({ mascota }) {
    return (
        <div className="bg-white rounded-lg shadow-md p-4 flex items-center justify-between">
            <div>
                <h3 className="text-lg font-bold text-gray-800">{mascota.nombre}</h3>
                <p className="text-sm text-gray-600">{mascota.especie} - {mascota.raza}</p>
            </div>
            {/* Aquí podríamos añadir botones para ver detalles o editar */}
            <Link href={`/mascotas/${mascota.id}/carnet`} className="text-indigo-600 hover:text-indigo-800 font-semibold">Ver Carnet</Link>
        </div>
    );
}

export default function MascotasPage() {
    const { user, loading: authLoading } = useAuth();
    const router = useRouter();
    const [mascotas, setMascotas] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (authLoading) return; // Esperar a que la autenticación termine
        if (!user) {
            router.replace('/login');
            return;
        }

        const mascotasRef = collection(db, 'users', user.uid, 'mascotas');
        const q = query(mascotasRef);

        const unsubscribe = onSnapshot(q, (snapshot) => {
            const mascotasData = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
            setMascotas(mascotasData);
            setLoading(false);
        }, (error) => {
            console.error("Error al obtener las mascotas:", error);
            setLoading(false);
        });

        return () => unsubscribe(); // Limpiar el listener al desmontar
    }, [user, authLoading, router]);

    if (loading || authLoading) {
        return <div className="text-center py-10">Cargando tus mascotas...</div>;
    }

    return (
        <main className="container mx-auto px-4 py-8">
            <div className="flex justify-between items-center mb-6">
                <h1 className="text-3xl font-bold">Mis Mascotas</h1>
                <Link href="/mascotas/nueva" className="bg-indigo-600 hover:bg-indigo-700 text-white font-bold py-2 px-4 rounded-lg shadow-md transition-colors duration-200">
                    + Agregar Mascota
                </Link>
            </div>

            {mascotas.length === 0 ? (
                <div className="text-center bg-white p-12 rounded-lg shadow-md">
                    <h2 className="text-xl font-semibold text-gray-700">Aún no has registrado ninguna mascota.</h2>
                    <p className="text-gray-500 mt-2 mb-6">¡Tu compañero fiel merece estar aquí! Añádelo para llevar un control de su salud y turnos.</p>
                    <Link href="/mascotas/nueva" className="bg-green-500 hover:bg-green-600 text-white font-bold py-3 px-6 rounded-lg text-lg shadow-lg transition-transform duration-200 hover:scale-105">
                        Agregar mi primera mascota
                    </Link>
                </div>
            ) : (
                <div className="space-y-4">
                    {mascotas.map(mascota => (
                        <MascotaCard key={mascota.id} mascota={mascota} />
                    ))}
                </div>
            )}
        </main>
    );
}
